class Section1 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: 'Divisa 1',
            selectedOption2: 'Divisa 2',
            texto: null,
            conversion: null,
            show: false,
            x: null,
            y: null,
            z: null,
            a: null,
            b: null,
            c: null,

        }
    }

    handleChange = selectedOption => {
        this.setState({ selectedOption: selectedOption });
        console.log(`Option selected 1:`, selectedOption);

    };
    handleChange2 = selectedOption2 => {
        this.setState({ selectedOption2: selectedOption2 });
        console.log(`Option selected 2:`, selectedOption2);
    };

    handleChangeText = (event) => {
        event.preventDefault();
        console.log(event)
        console.log(event.target.value)
        this.setState({
            texto: event.target.value
        });
    }


    async getOptions(divisaA, divisaB, text) {
        console.log("texto solo: " + text);
        console.log(text.value);
        const show = true;
        if ((divisaA !== null && divisaB !== 'undefined') && (divisaA !== divisaB) && (text !== null)) {
            const url = 'https://v6.exchangerate-api.com/v6/aac9b36d86d77623835d5a6c/pair/' + divisaA + '/' + divisaB;
            console.log(url)
            const res = await axios.get(url);
            const data = res.data.conversion_rate;
            console.log(data)
            const conversion = (parseFloat(data) * parseFloat(text)).toFixed(2);
            const x = data.toFixed(2);
            const y = (data * 10).toFixed(1);
            const z = Math.round(data * 100);
            const a = (1/data).toFixed(5)
            const b = (10/data).toFixed(4)
            const c = (100/data).toFixed(3)
            this.setState({ conversion: conversion });
            this.setState({ show: show});
            this.setState({ x: x});
            this.setState({ y: y});
            this.setState({ z: z});
            this.setState({ a: a});
            this.setState({ b: b});
            this.setState({ c: c});
        }
        else {
            alert("Debes llenar todos los cuadros")
        }

    }
    render() {
        const { selectedOption } = this.state;
        const { selectedOption2 } = this.state;
        const { texto } = this.state;
        const { conversion } = this.state;
        const { x } = this.state;
        const { y } = this.state;
        const { z } = this.state;
        const { a } = this.state;
        const { b } = this.state;
        const { c } = this.state;
        if (!this.state.show) {
            return (
                <section>
                    <div className="container">
                        <div className="jumbotron contenedor">
                            <div className="row">
                                <div className="col actual">
                                    <p className="mb-2">De: </p>
                                    <Select name='select1' className="mb-2"
                                        value={selectedOption}
                                        onChange={this.handleChange}
                                        options={options}
                                        placeholder="Seleccione"
                                    />
                                    <p>Importe</p>
                                    <input className="inp" type="number" placeholder="Ingrese cantidad" id="actualCurrency" min="0"
                                        onChange={this.handleChangeText}
                                    ></input>
                                </div>
                                <div className="col actual">
                                    <p className="mb-2">A: </p>
                                    <Select name='select2' className="mb-2"
                                        value={selectedOption2}
                                        onChange={this.handleChange2}
                                        options={options}
                                        placeholder="Seleccione"
                                    />
                                    <p>Importe</p>
                                    <p className="cuadro">{conversion}</p>
                                </div>
                            </div>
                            <button className="btn btn-info mt-3 btm" onClick={() => this.getOptions(selectedOption.value, selectedOption2.value, texto)}>Convertir</button>
                        </div>
                    </div>
                </section>

            )
        }
        else {
            return (
                <>
                    <section>
                        <div className="container">
                            <div className="jumbotron contenedor">
                                <div className="row">
                                    <div className="col actual">
                                        <p className="mb-2">De: </p>
                                        <Select name='select1' className="mb-2"
                                            value={selectedOption}
                                            onChange={this.handleChange}
                                            options={options}
                                            placeholder="Seleccione"
                                        />
                                        <p>Importe</p>
                                        <input className="inp" type="number" placeholder="Ingrese cantidad" id="actualCurrency" min="0"
                                            onChange={this.handleChangeText}
                                        ></input>
                                    </div>
                                    <div className="col actual">
                                        <p className="mb-2">A: </p>
                                        <Select name='select2' className="mb-2"
                                            value={selectedOption2}
                                            onChange={this.handleChange2}
                                            options={options}
                                            placeholder="Seleccione"
                                        />
                                        <p>Importe</p>
                                        <p className="cuadro">{conversion}</p>
                                    </div>
                                </div>
                                <button className="btn btn-info mt-3 btm" onClick={() => this.getOptions(selectedOption.value, selectedOption2.value, texto)}>Convertir</button>
                            </div>
                        </div>

                        <h3 className="cosa">Detalles divisa:</h3>

                        <div className="container">
                            <table className="table table-bordered">
                                <thead>
                                    <tr>
                                        <th>{selectedOption.label} / {selectedOption.value}</th>
                                        <th>{selectedOption2.label} / {selectedOption2.value}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>1 {selectedOption.value}</td>
                                        <td>{x} {selectedOption2.value}</td>
                                    </tr>
                                    <tr>
                                        <td>10 {selectedOption.value}</td>
                                        <td>{y} {selectedOption2.value}</td>
                                    </tr>
                                    <tr>
                                        <td>100 {selectedOption.value}</td>
                                        <td>{z} {selectedOption2.value}</td>
                                    </tr>
                                    <tr>
                                        <td>{a} {selectedOption.value}</td>
                                        <td>1 {selectedOption2.value}</td>
                                    </tr>
                                    <tr>
                                        <td>{b} {selectedOption.value}</td>
                                        <td>10 {selectedOption2.value}</td>
                                    </tr>
                                    <tr>
                                        <td>{c} {selectedOption.value}</td>
                                        <td>100 {selectedOption2.value}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </section>
                </>

            )
        }
    }
}

export default Section1;

